.product {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  margin: 10px;
  padding: 15px;
  width: 100%;
  max-height: 400px;
  min-width: 100px;
  background-color: white;
  z-index: 1;
  /* transition: transform 0.1s; */
}

.product:hover {
  transform: scale(1.02);
}
.product__rating {
  display: flex;
}

.product > img {
  max-height: 200px;
  width: 100%;
  object-fit: contain;
  margin-bottom: 15px;
}

.product > button {
  background: #f0c14b;
  border: 1px solid;
  margin-top: 10px;
  border-color: #a88734 #9c7e31 #846a29;
  color: #111;
  cursor: pointer;
}
.product > button:hover {
  font-weight: 600;
}

.product__price {
  margin-top: 5px;
}

.product__info {
  height: 100px;
  margin-bottom: 15px;
  /* width: 100%; */
}
@media screen and (max-width: 500px) {
  .product {
    display: flex;
    flex-direction: column-reverse;
    width: 80%;
    padding: 10px;
  }
  .product > img {
    max-height: 150px;
    margin-top: 20px;
  }
}
@media screen and (max-width: 415px) {
  .product {
    width: 87%;
  }
}
