.checkout {
  display: flex;
  background-color: white;
  height: max-content;
  padding: 20px;
}

.checkout__ad {
  width: 100%;
  margin-bottom: 10px;
}
.checkout__title {
  margin-right: 10px;
  padding: 10px;
  border-bottom: 1px solid lightgray;
}
@media screen and (max-width: 500px) {
  .checkout {
    display: block;
  }
}
@media screen and (max-width: 415px) {
  .checkout {
    display: block;
  }
  .checkout__ad {
    width: 0px;
  }
}
